		font-size: 90%;
	}

	#main-wrapper {
		margin-left: 210px;
	}

	#breadcrumb {
		width: 65%;
		overflow: hidden;
		text-overflow: ellipsis;
		word-break: keep-all;
		white-space: nowrap;
	}
}

/* panel hidden */
@media all and (max-width: 1199px) {
	#panel-wrapper {
		display: none;
	}

	#topbar {
		padding: 0;
	}

	#main > div.row {
		-webkit-box-pack: center !important;
